<template>
    <div >
        <div class="h-headtitle">获取视频第一帧图片</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                用于获取视频第一帧图片，最终图片是base64格式。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <input type="file" id="upload-ipt"
                       @change="chooseVideoGetFirstFrameImg($event,{videoDivId:'vId',picWidth:500,picHei:300})"
                       accept="video/*" />
                <div id="vId" style="width: 500px;height: 300px;border:1px solid #ccc;margin-top:10px;"></div>

                <div style="margin-top:20px;">第一帧图片展示</div>
                <div style="width: 500px;height: auto">
                    <img :src="firstImgUrl" width="100%" />
                </div>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code>
                        //定义一个file类型按钮
                        &lt;input type="file" id="upload-ipt"
                               @change="chooseVideoGetFirstFrameImg($event,{videoDivId:'vId',picWidth:500,picHei:300})"
                               accept="video/*" /&gt;
                        //定义一个div并且加上id，也可以用其它标签，有个id就行,用于创建video标签时候存放,不要展示时候，可以灵活隐藏掉
                        &lt;div id="vId"&gt;&lt;/div&gt;
                        &lt;div&gt;第一帧图片展示&lt;/div&gt;
                        &lt;div&gt;
                        &lt;img :src="firstImgUrl" width="100%" /&gt;
                        &lt;/div&gt;
                        //主要方法
                        chooseVideoGetFirstFrameImg(e,obj) {
                            let self = this;
                            //逻辑判断，防止少传
                            if(Object.keys(arguments[1]).length < 3){
                                console.log('缺少参数');
                                return;
                            }
                            if(!obj.videoDivId){
                                console.log('请传videoDivId');
                                return;
                            }
                            if(!obj.picWidth){
                                console.log('请传picWidth');
                                return;
                            }
                            if(!obj.picHei){
                                console.log('请传picHei');
                                return;
                            }
                            let files = e.target.files[0];//获取文件
                            let blob = new Blob([files]); // 文件转化成二进制文件
                            let url = URL.createObjectURL(blob); //转化成url
                            let parent = document.getElementById(obj.videoDivId);
                            let div = document.createElement("div");
                            div.setAttribute("class", "newDivVideo");
                            parent.appendChild(div);
                            document.querySelector('.newDivVideo').innerHTML = '&lt;video controls src="'+url+'" id="videoId" width="100%" height="100%" autoplay&gt;&lt;/video&gt;';
                            let videoElement = document.querySelector('#videoId');
                            videoElement.addEventListener("canplay", function() {
                                let canvas = document.createElement("canvas");
                                canvas.width = obj.picWidth;
                                canvas.height = obj.picHei;
                                canvas.getContext("2d").drawImage(videoElement, 0, 0, canvas.width, canvas.height);
                                let outImgUrl = canvas.toDataURL("image/png");
                                console.log(outImgUrl); //第一帧图片url
                                self.getImg(outImgUrl); //业务逻辑处理
                            })
                        },
                        getImg(data){
                            this.firstImgUrl = data;
                        }
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="380">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                firstImgUrl:'',
                tableData: [
                    {
                        param: 'videoDivId',
                        desc: '定义一个div并且加上id，也可以用其它标签，有个id就行,用于创建video标签时候存放',
                        type: 'String',
                        defaultVal: '',
                    },
                    {
                        param: 'picWidth',
                        desc: '截图第一帧图片的宽度',
                        type: 'Number',
                        defaultVal: '',
                    },
                    {
                        param: 'picHei',
                        desc: '截图第一帧图片的高度',
                        type: 'Number',
                        defaultVal: '',
                    }
                ],
            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode() {
                $('.view-code-btn').click(function () {
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
            chooseVideoGetFirstFrameImg(e,obj) {
                let self = this;
                if(Object.keys(arguments[1]).length < 3){
                    console.log('缺少参数');
                    return;
                }
                if(!obj.videoDivId){
                    console.log('请传videoDivId');
                    return;
                }
                if(!obj.picWidth){
                    console.log('请传picWidth');
                    return;
                }
                if(!obj.picHei){
                    console.log('请传picHei');
                    return;
                }
                let files = e.target.files[0];
                let blob = new Blob([files]); // 文件转化成二进制文件
                let url = URL.createObjectURL(blob); //转化成url
                let parent = document.getElementById(obj.videoDivId);
                let div = document.createElement("div");
                div.setAttribute("class", "newDivVideo");
                parent.appendChild(div);
                document.querySelector('.newDivVideo').innerHTML = '<video controls src="'+url+'" id="videoId" width="100%" height="100%" autoplay></video>';
                let videoElement = document.querySelector('#videoId');
                videoElement.addEventListener("canplay", function() {
                    let canvas = document.createElement("canvas");
                    canvas.width = obj.picWidth;
                    canvas.height = obj.picHei;
                    canvas.getContext("2d").drawImage(videoElement, 0, 0, canvas.width, canvas.height);
                    let outImgUrl = canvas.toDataURL("image/png");
                    console.log(outImgUrl); //第一帧图片url
                    self.getImg(outImgUrl); //业务逻辑处理
                })
            },
            getImg(data){
                this.firstImgUrl = data;
            }
        },
        components:{

        }
    }
</script>
<style scoped>

</style>



